<template>
  <v-container fluid class="homepage">
    <v-row class="px-2">        
        <v-col cols="12">
            <h1 class="ml-4">Global Crypto Stats</h1>
            <v-row class="px-5">
                <v-col cols=6>
                        <h3>Total cryptocurrencies</h3>
                    <p>
                        <span> {{global.active_cryptocurrencies || 0}} </span>
                    </p>
                </v-col>
                <v-col cols="6">
                        <h3>Total exchanges</h3>
                    <p>
                        <span> {{global.markets || 0}} </span>
                    </p>
                </v-col>
                <v-col cols="6">
                        <h3>Total market cap</h3>
                    <p>
                        <span> {{roundNumber(global.total_market_cap)}} </span>
                    </p>
                </v-col>
                <v-col cols="6">
                        <h3>Total 24h volume</h3>
                    <p>
                        <span> {{roundNumber(global.total_volume)}} </span>
                    </p>
                </v-col>
            </v-row>
            <v-row class="mx-3">
                <v-col cols="10">
                    <h1>Top 10 cryptocurrencies in the world</h1>
                </v-col>
                <v-col class="text-end">
                    <v-btn href="/cryptocurrencies" x-large text color="blue">Show More</v-btn>                    
                </v-col>
            </v-row>
            <Cryptocurrencies :searchbar='false' :show="10" />
            <v-row class="mx-3">
                <v-col cols="10">
                    <h1>Top 10 cryptocurrencies in the world</h1>
                </v-col>
                <v-col class="text-end">
                    <v-btn href="/news" x-large text color="blue">Show More</v-btn>                    
                </v-col>
            </v-row>
            <News :searchbar='false' :show="10"/>
        </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { millify } from 'millify';
import {mapState} from 'vuex';
import Cryptocurrencies from './Cryptocurrencies.vue'
import News from './News.vue'
export default {
    components: {
        News,
        Cryptocurrencies,
    },
    mounted () {
        this.$store.dispatch('getGlobal')
    },
        methods: {
        roundNumber(number) {
            if(number == undefined){
                return 0
            } else{
                return millify(number.usd,{
                    precision:2,
                }) 
            }
        }
    },
    computed: mapState([
        "global"
    ])
};
</script>

