<template>
<div>
    <v-row v-if="searchbar == true" class="pt-5 px-5">
        <v-col cols="10" sm="5" md="3">
            <v-select outlined clearable dense @change="cryptoNews(crypto)" v-model="crypto" :items="selectCrypto()"></v-select>
        </v-col>
    </v-row>
    <v-row class="px-5 py-5">
        <v-col xs="12" sm="6" md="4" v-for="(article,id) in news.slice(0,this.show)" :key="id">
                <v-card :height="$vuetify.breakpoint.lgAndDown ? '22em' : '19em' " target="_blank" :href="article.link" style="background-color:#E0F2F1; cursor:pointer" hoverable class="px-5 py-5">
                    <v-row dense class="px-1 pb-3 justify-space-between">
                        <v-col cols="7" md="6">
                            <h3>{{article.title.length > 75 ? article.title.substring(0,75) + "..." : article.title }}</h3>
                        </v-col>
                        <v-col cols="5" md="6">
                            <v-img width="170px" height="105px" alt="newsImage" :src="article.media"></v-img>
                        </v-col>
                    </v-row>
                    <span>{{article.summary.length > 220 ? article.summary.substring(0,220) + "..." : article.summary }}</span>
                    <v-row style="position:absolute;bottom:6px;right:10px">
                        <v-col class="text-end">
                            {{formatTime(article.published_date)}}
                        </v-col>
                    </v-row>                   
                </v-card>
        </v-col>
    </v-row>
</div>
</template>

<script>
import moment from "moment";
import {mapState} from 'vuex';
export default {
    mounted () {
        this.$store.dispatch('getNews',{
            query:"Cryptocurrency"
        });
        this.$store.dispatch('getCoinsList');
    },
    methods: {
        cryptoNews(crypto){
            if(crypto == null){
                this.news = []
                this.$store.dispatch('getNews',{
                query:'Cryptocurrency'
            });
            } else {
                this.news = []
                this.$store.dispatch('getNews',{
                query:crypto
            });
            }
        },
        formatTime(time){
           return moment(time).startOf('hour').fromNow()
        },
        selectCrypto(){
            const list = []
            this.coinsList.forEach(coin => {
                list.push(coin.name)              
            });
            return list
        }
    },
    computed: {
        ...mapState(['news',"coinsList"]),
    },
    props: {
        show:{
            type: Number,
            default:25,
        },
        searchbar:{
            type: Boolean,
            default:true,
        },
    },
    data() {
        return {
            crypto: '',
        }
    },

}
</script>

