<script>
import Chart from "./Chart.vue"
import { millify } from 'millify';
import {mapState} from 'vuex';
export default {
    components: {
        Chart,
    },
    mounted () {
        this.$store.dispatch('getCoin', {
            coin: this.$route.params.coin
        });
    },
    computed: mapState([
        "coin"
    ]),
    methods: {
        roundNumber(number) {
            if(number === undefined){
                return 0
            } else{
                return millify(number.usd,{
                    precision:2,
                }) 
            }
        },
        roundPercentage(number){
                return millify(number, {
                precision:1,
                units:['%']
            }) 
            
        },
    },

}
</script>

