import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import moment from "moment";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    news:[],
    exchanges:[],
    global:[],
    coinsList:[],
    coin:{},
    chartData:{
      labels: [],
      datasets: [
        {
          label: "Market Data",
          backgroundColor: "#3076f0",
          fill: false,
          borderColor: "#3076f0",
          data: [],
          pointRadius: 0
        },
      ]
    },
    loadingStatus:false,
  },
  mutations: {
    SET_LOADING_STATUS(state, newLoadingStatus){
      state.loadingStatus = newLoadingStatus
    },
    SET_GLOBAL(state,data){
      state.global = data
    },
    SET_COIN(state,data){
      state.coin = data
    },
    SET_COINSLIST(state,data){
      state.coinsList = data
    },
    SET_COIN_CHART(state,data){
        data.prices.forEach(element => {
          state.chartData.labels.push(moment(element[0]).locale('fr').format('l,LT'))
          state.chartData.datasets[0].data.push(element[1])
        });
    },
    SET_NEWS(state,data){
      state.news = data.articles
    },
    SET_EXCHANGES(state,data){
      state.exchanges = data
    },
  },
  actions: {
    async getGlobal(context){
      try {
        const response = await axios.get("http://localhost:5000/global");
        context.commit("SET_GLOBAL", response.data.data)        
      } catch (error) {
        console.log(error);
      }
    },
    async getCoin(context,coin){
      try {
        const response = await axios.get(`http://localhost:5000/coins/${coin.coin}`);
        context.commit("SET_COIN", response.data)        
      } catch (error) {
        console.log(error);
      }
    },
    async getCoinsList(context){
      try {
        this.commit('SET_LOADING_STATUS', true)
        const response = await axios.get("http://localhost:5000/coins/markets");
        context.commit("SET_COINSLIST", response.data)
        this.commit('SET_LOADING_STATUS', false)
        
      } catch (error) {
        console.log(error);
      }
    },
    async getCoinChart(context,coin){
      try {
        const response = await axios.get(`http://localhost:5000/coins/${coin.coin}/market_chart`, {
         params: {timeframe : coin.timeframe},
        });
        context.commit("SET_COIN_CHART", response.data, coin.timeframe)        
      } catch (error) {
        console.log(error);
      }      
    },
    async getNews(context,query){
      try {
        const response = await axios.get('http://localhost:5000/news', {
          params: {query : query.query},
        });
        context.commit("SET_NEWS", response.data)        
      } catch (error) {
        console.log(error);
      }      
    },
    async getExchanges(context){
      try {
        const response = await axios.get('http://localhost:5000/exchanges');
        context.commit("SET_EXCHANGES", response.data)        
      } catch (error) {
        console.log(error);
      }      
    }
  },
  modules: {},
});
