<template>
 <v-container fluid>
    <v-row>
      <v-col offset-md="1" cols="6" sm="3" md="2">
        <v-select class="pl-4" dense :placeholder="currentTimeframe" @change="changeTimeframe(currentTimeframe)" v-model="currentTimeframe" :items="selectTimeframe"></v-select>
      </v-col>
    </v-row>
    <v-row class="justify-center">
      <v-col cols="12" md="10">
        <v-row class="px-7">
            <h2>
              {{coin.name}} Price Chart
            </h2>
            <v-spacer></v-spacer>
            <h3>
            Current Price : {{coin.market_data.current_price.usd}} $
            </h3>
        </v-row>
        <LineChartGenerator ref="line" :width="width" :height="height" :chart-options="chartOptions" :chart-data="chartData" />
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import {mapState} from 'vuex';
import { Line as LineChartGenerator } from "vue-chartjs/legacy";
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  LineElement,
  LinearScale,
  CategoryScale,
  PointElement,
} from "chart.js";
ChartJS.register(
  Title,
  Tooltip,
  Legend,
  LineElement,
  LinearScale,
  CategoryScale,
  PointElement
);

export default {
  name: "LineChart",
  components: {
    LineChartGenerator,
  },
  data() {
    return {
        selectTimeframe: ["1 day", "7 days", "14 days", "30 days","3 months","1 year","5 years","Max"],
        currentTimeframe:"7 Days",
        chartTimeframe:7,
        width:1200,
        height:600,
      chartOptions: {
        responsive: true,
        maintainAspectRatio: false,
      },
    };
  },
    mounted () {
        this.chartData.labels = []
        this.chartData.datasets[0].data = []
        this.$store.dispatch('getCoinChart', {
            timeframe:7,
            coin: this.$route.params.coin,
        });
        this.$store.dispatch('getCoin', {
            coin: this.$route.params.coin
        });
    },
    computed: {
      ...mapState(['chartData',"coin"])
    },
    methods: {
        changeTimeframe(timeframe) {
          this.chartData.labels = []
          this.chartData.datasets[0].data = []
            switch (timeframe) {
                case "1 day":                    
                    this.chartTimeframe = 1                   
                    break;
                case "7 days":                    
                    this.chartTimeframe = 7                  
                    break;
                case "14 days":                    
                    this.chartTimeframe = 14                    
                    break;
                case "30 days":                    
                    this.chartTimeframe = 30             
                    break;
                case "3 months":                    
                    this.chartTimeframe = 90             
                    break;
                case "1 year":                    
                    this.chartTimeframe = 365             
                    break;
                case "5 years":                    
                    this.chartTimeframe = 1825             
                    break;
                case "Max":                    
                    this.chartTimeframe = 'max'             
                    break;
                default:
                    break;
            }
            this.$store.dispatch('getCoinChart', {
            timeframe:this.chartTimeframe,
            coin: this.$route.params.coin,
        });
        }
        
    }
}
</script>

