<template>
<div>
    <v-row v-if="searchbar == true" class="pt-5 px-5">
        <v-col cols="10" sm="5" md="3">
            <v-text-field append-outer-icon="mdi-magnify" outlined dense  v-model="search" ></v-text-field>
        </v-col>
    </v-row>
    <v-row class="px-5 py-5">
        <v-col @click="goTo(coin.id)" xs="12" sm="6" md="4" lg="3" v-for="coin in filteredList.slice(0,this.show)" :key="coin.market_cap">
                <v-card style="background-color:#E0F2F1; cursor:pointer" hoverable class="crypto-card px-5 py-5">
                    <v-row dense class="px-1 pb-3 align-start justify-space-between">
                        <h3>{{coin.market_cap_rank}}. {{coin.name}}</h3>
                        <v-img max-width="40px" alt="CryptoLogo" :src="coin.image"></v-img>
                    </v-row>
                    <v-divider class="py-2"></v-divider>
                    <p>Price: {{roundNumber(coin.current_price)}} $</p>
                    <p>Market Cap: {{roundNumber(coin.market_cap)}} $</p> 
                    <p>Daily Change: 
                        <span :class="(coin.price_change_percentage_24h < 0) ? 'red--text' : 'green--text'">
                            {{roundNumber(coin.price_change_percentage_24h)}} %
                        </span>
                    </p>
                </v-card>
        </v-col>
    </v-row>
</div>
</template>

<script>
import { millify } from 'millify';
import {mapState} from 'vuex';
export default {
    mounted () {
        this.$store.dispatch('getCoinsList');
    },
    computed: {
        ...mapState(['coinsList']),
        filteredList(search){
            if (search != "") {
                return this.coinsList.filter(coin => {
                    return coin.name.toLowerCase().includes(this.search.toLowerCase())
                })
            } else return this.coinsList
        }
    },
    data() {
        return {
            search: '',
        }
    },
    props: {
        show:{
            type: Number,
            default:100,
        },
        searchbar:{
            type: Boolean,
            default:true,
        },
    },
    methods: {
        goTo(id){
            this.$router.push({
                name: 'CryptoDetails',
                params:{coin: id}
            })
        },
        roundNumber(number) {
            return millify(number, {
                precision:3,
                units:['','','M','B','T','P','E']
            }) 
        },
    },

}
</script>

