<template>
   <v-container fluid class="pt-8">
      <v-row>
        <v-col>
          <v-expansion-panels>
            <v-row :class="header">
              <v-col cols="3">
                <span>
                  Exchanges
                </span>
              </v-col>
              <v-col cols="3">
                <span>
                  24h Trade Volume
                </span>
              </v-col>
              <v-col cols="3">
                <span>
                  Country Of Registration
                </span>
              </v-col>
              <v-col cols="3">
                <span>
                  Date Of Creation
                </span>
              </v-col>
            </v-row>
            <v-expansion-panel v-for="(exchange,id) in exchanges" :key="id">
              <v-expansion-panel-header>
                <v-row>
                  <v-col cols="3">
                    <span>
                      {{id+1}} .
                    </span>
                    <v-avatar size="30px">
                      <v-img max-height="20px" contain :src="exchange.image" alt="exchangeLogo"></v-img>
                    </v-avatar>
                    <span>{{exchange.name}} </span>
                  </v-col>
                  <v-col cols="3">
                    ฿ {{roundNumber(exchange.trade_volume_24h_btc)}}
                  </v-col>
                  <v-col cols="3">
                    {{exchange.country == null ? "Decentralized Exchange" : exchange.country}}
                  </v-col>
                  <v-col cols="3">
                    <span>{{exchange.year_established == null ? "N/A" : exchange.year_established}} </span>
                  </v-col>
                </v-row>
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                <p>{{exchange.description == "" ? "No description provided :( " : exchange.description}} </p>
                <p>Trust score : {{exchange.trust_score}}/10</p>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-col>
      </v-row>
    </v-container>
</template>

<script>
import { millify } from 'millify';
import {mapState} from 'vuex';
export default {
    mounted () {
        this.$store.dispatch('getExchanges');
    },
    computed: {
        ...mapState(['exchanges']),
        header(){
          if(this.$vuetify.breakpoint.smAndDown){
            return ""
          } else return "pr-11"
        }
    },
    methods: {
        roundNumber(number) {
          return millify(number,{
            precision:2,
          }) 
        },
    },

}
</script>

